import { Input } from '@angular/core';

import { convertToBoolean } from '../../utils/util';
import { PoButtonGroupItem } from './po-button-group-item.interface';
import { PoButtonGroupToggle } from './po-button-group-toggle.enum';

const PO_TOGGLE_TYPE_DEFAULT = 'none';

/**
 * @description
 *
 * O componente `po-button-group` Ã© formado por um conjunto de botÃµes distribuÃ­dos horizontalmente.
 * Cada botÃ£o do grupo Ã© tratado de forma individual, recebendo assim um rÃ³tulo, uma aÃ§Ã£o bem como se deverÃ¡ estar habilitado ou nÃ£o.
 *
 * Este componente alÃ©m de servir como um agrupador de botÃµes para aÃ§Ã£o, tambÃ©m permite que sejam utilizados
 * para seleÃ§Ãµes multiplas e Ãºnicas.
 *
 * O grupo de botÃµes deve ser utilizado para organizar as aÃ§Ãµes de maneira uniforme e transmitir a ideia de que os botÃµes fazem
 * parte de um mesmo contexto.
 *
 * #### Boas prÃ¡ticas
 *
 * - Evite usar o `po-button-group` com apenas 1 aÃ§Ã£o, para isso utilize o `po-button`.
 * - Procure utilizar no mÃ¡ximo 3 aÃ§Ãµes para cada `po-button-group`.
 *
 * > As recomendaÃ§Ãµes do `po-button` tambÃ©m valem para o `po-button-group`.
 */
export class PoButtonGroupBaseComponent {

  private _small?: boolean = false;
  private _toggle?: string = PO_TOGGLE_TYPE_DEFAULT;

  /** Lista de botÃµes. */
  @Input('p-buttons') buttons: Array<PoButtonGroupItem> = [];

  /**
   * @optional
   *
   * @description
   *
   * Torna o grupo de botÃµes com tamanho minificado.
   *
   * @default `false`
   */
  @Input('p-small') set small(value: boolean) {
    this._small = <any>value === '' ? true : convertToBoolean(value);
  }

  get small(): boolean {
    return this._small;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o modo de seleÃ§Ã£o de botÃµes.
   *
   * > Veja os valores vÃ¡lidos no *enum* `PoMultiselectFilterMode`.
   *
   * @default `none`
   */
  @Input('p-toggle') set toggle(value: string) {
    this._toggle = (<any>Object).values(PoButtonGroupToggle).includes(value) ? value : PO_TOGGLE_TYPE_DEFAULT;

    this.checkSelecteds(this._toggle);
  }

  get toggle(): string {
    return this._toggle;
  }

  onButtonClick(buttonClicked: PoButtonGroupItem, buttonIndex: number) {
    if (this.toggle === PoButtonGroupToggle.Single) {
      this.buttons.forEach((button, index) =>
        button.selected = index === buttonIndex ? !buttonClicked.selected : false);
    } else if (this.toggle === PoButtonGroupToggle.Multiple) {
      buttonClicked.selected = !buttonClicked.selected;
    }
  }

  private checkSelecteds(toggleMode: string) {
    if (toggleMode === PoButtonGroupToggle.None) {
      this.deselectAllButtons();
    } else if (toggleMode === PoButtonGroupToggle.Single) {
      const hasMoreOneSelected = this.buttons.filter(button => button.selected).length > 1;
      if (hasMoreOneSelected) {
        this.deselectAllButtons();
      }
    }
  }

  private deselectAllButtons() {
    this.buttons.forEach(button => button.selected = false);
  }

}
